
import {Line as LineChartGenerator} from 'vue-chartjs/legacy'
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  CategoryScale,
  PointElement
} from 'chart.js'

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    CategoryScale,
    PointElement
)
export default {
  props: {
    chartid: {
      type: String,
      default: "chart"
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 400
    },
    height: {
      type: Number,
      default: 400
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {
      }
    },
    plugins: {
      type: Array,
      default: () => []
    },
    chartoptions: {
      type: Object,
      default: () => {
      }
    },
    chartdata: {
      type: Object,
      default: () => {
      }
    }
  },
  name: 'LineChart',
  components: {LineChartGenerator},
  mounted() {
    console.log('Chart ready: ', this.chartID, this.chartdata, this.chartoptions)
  }
}
